import * as axios from 'axios'
import { NavLink } from 'react-router-dom'
import Slider from 'react-slick'
import serv from './service.module.scss'



function Service(props) {



   if (props.serviceItems.length === 0) {
      axios.get('http://localhost:3333/services').then(response => {
         props.serviceHot(response.data)
      })
   }
   if (props.styleItems.length === 0) {
      axios.get('http://localhost:3333/thems').then(response => {
         props.styleHot(response.data)
      })
   }

   const settings = {
      dots: true,
      focusOnSelect: true,// щелкнуть на слайд чтобы прокрутилось до него
      fade: false, //одно изображение при true
      lazyLoad: true, //медленная загрузка
      pauseOnHover: true, //Пауза при наведении
      infinte: true, // Неравномерные множества
      speed: 750, //скорость перелистывания
      slidesToShow: 2, //Количество видимых слайдов сразу
      // arrows: true, //Стрелки вперёд назад
      nextArrow: <div className={serv.imgPrew}>2</div>,
      prevArrow: <div className="raz">1</div>,
      slidesToScroll: 1, //Количество перелист блоков за раз
      className: "servicesMiniSlides", //Класс у контейнера
      autoplay: false, //авто запуск
      autoplaySpeed: 1000,//врямя до первого авто запуска потом нужно вычитать скорость прокрутки
      // centerMode: true
      responsive: [
         {
            breakpoint: 1050,
            settings: {
               slidesToShow: 2
            }
         },
         {
            breakpoint: 715,
            settings: {
               slidesToShow: 1
            }
         }
      ]
   }

   let slidService = props.serviceItems.map(ss => <NavLink to={`services/${ss.linkMore}`} key={ss.id}><div style={{ width: `100%`, maxWidth: `340px` }}>
      <div className={serv.imgPrew} style={{ backgroundImage: `url(${ss.img})`, height: `200px` }}></div>
      <div>
         <div><h3>{ss.nameService}</h3></div>
         <div>{ss.about.mini.map(s=><p>{s.text}</p>)}</div>
         <div>{ss.termsOfService}</div>
      </div>
   </div>
   </NavLink>
   )

   return (
      <div>
         <h2>Услуги</h2>
         <Slider {...settings}>
            {slidService}
         </Slider>
      </div>

   )
}


export default Service



