import {
    hotelApi
} from "../api/api"

const PROMOTIONS_RED = 'PROMOTIONS-RED'
const FULL_PROMOTIONS = 'FULL-PROMOTIONS'
const SET_PROMOTIONS = 'SET-PROMOTIONS'

let initionState = {
    promotionItems: [],
    sliderItems: [{
            id: 1,
            title: 'Фото 1',
            urlPhoto: 'https://cdn.pixabay.com/photo/2015/09/28/21/32/the-palm-962785_960_720.jpg',
        },
        {
            id: 2,
            title: 'Фото 2',
            urlPhoto: 'https://cdn.pixabay.com/photo/2017/06/07/08/56/girl-2379705_960_720.jpg',
        },
        {
            id: 3,
            title: 'Фото 3',
            urlPhoto: 'https://image.freepik.com/free-photo/luxury-bedroom-suite-in-resort-high-rise-hotel-with-working-table_105762-1783.jpg',
        },
        {
            id: 4,
            title: 'Фото 4',
            urlPhoto: 'https://image.freepik.com/free-photo/beautiful-luxury-outdoor-swimming-pool-in-hotel-and-resort_74190-7433.jpg',
        },
        {
            id: 5,
            title: 'Фото 5',
            urlPhoto: 'https://www.onetwotrip.com/ru/blog/wp-content/uploads/2017/08/read.jpg',
        },
        {
            id: 6,
            title: 'Фото 6',
            urlPhoto: 'https://image.freepik.com/free-photo/picture-of-guests-getting-key-card-in-hotel_109285-738.jpg',
        },
    ],
    sPromotion: null,
}


const homeReducer = (state = initionState, action) => {
    switch (action.type) {
        case PROMOTIONS_RED: {
            return {
                ...state,
                promotionItems: [ /*...state.promotionItems,*/ ...action.promotionItems]
            }
        }
        case FULL_PROMOTIONS: {
            return {
                ...state,
                promotionItems: [ /*...state.promotionItems,*/ ...action.promotionItems]
            }
        }
        case SET_PROMOTIONS: {
            return {
                ...state,
                sPromotion: action.sPromotion
            }
        }
        default:
            return state;
    }
}
export const promotionAC = (promotionItems) => ({
    type: PROMOTIONS_RED,
    promotionItems
})
const setPromotion = (sPromotion) => ({
    type: SET_PROMOTIONS,
    sPromotion
})
export const getPromotion = (linkMore) => (dispatch) => {
    hotelApi.getPromotion(linkMore).then(response => {
        dispatch(setPromotion(response.data))
    })
}
export default homeReducer;