
import React from "react";
import { NavLink } from "react-router-dom";
import CFpromotion from "./cardPromotion/CFpromotion";
import fp from "./fp.module.scss"
import CContainer from "../../../home/contactShow/CContainer";

function FullPromotion(props) {
    if (!props.sPromotion) {
        return <div>Загрузка информации...</div>
    }
    if (!props.sStyle) {
        return <div>Загрузка стилей...</div>
    }

    return (
        <>
            <div>
                <div className={fp.promoImg} style={{ backgroundImage: `url(${props.sPromotion.promotions.full.img})` }}>
                </div>
                <div className="main-cont">
                    <div style={{ marginTop: `30px` }} className={fp.breadCrumbs}>
                        <NavLink to={props.sPromotion.breadCrumbs.lvlOne.link}>{props.sPromotion.breadCrumbs.lvlOne.name}</NavLink> / <NavLink to={props.sPromotion.breadCrumbs.lvlDown.link}>{props.sPromotion.breadCrumbs.lvlDown.name}</NavLink> / <b>{props.sPromotion.title}</b>
                    </div>
                    <div style={{ margin: `60px 0` }}>
                        <div className={fp.aboutCont}>
                            <div className={`${fp.promoImgMini} ${fp.aboutBlock}`} style={{ backgroundImage: `url(${props.sPromotion.promotions.full.img})` }}></div>
                            <div className={fp.aboutBlock}>
                                <div>
                                    <h2>{props.sPromotion.title}</h2>
                                </div>
                                <div>
                                    {props.sPromotion.promotions.mini.about}
                                </div>
                                <div>
                                    {props.sPromotion.promotions.full.about.map(terms => <p>{terms.line}</p>)}
                                </div>
                            </div>
                        </div>
                        <div className={fp.termOfAction}>
                            <div style={{ maxWidth: `700px` }}>
                                <div>
                                    <h3 style={{ textAlign: `center` }}>Условия акции:</h3>
                                </div>
                                <ul className={`push ${props.sStyle.map(s => s.subject)}-text`}>
                                    {props.sPromotion.promotions.full.termsOfaction.map(terms => <li>{terms.item}</li>)}
                                </ul>
                            </div>
                            <CFpromotion sPromotion={props.sPromotion} sStyle={props.sStyle} />
                        </div>
                        <hr />
                        <div>
                            <NavLink to='/promotions'><button className={`${props.sStyle.map(b => b.subject)} lighten-1`} style={{ textAlign: `center`, width: `180px`, padding: `8px 0`, margin: `15px auto 10px`, borderColor: `#fff0` }}><span className={`${props.sStyle.map(b => b.subject)}-text text-lighten-5`}>Вернуться назад</span></button></NavLink>
                        </div>
                    </div>
                </div>
                <CContainer />
            </div>
        </>
    );

}

export default FullPromotion