import st from "./socNet.module.scss";


function SocNetwork(props) {
    let sStyle = props.sStyle.map(s => s.subject)

    return (
        <div className={st.cont}>
            {props.soc.map(soc => <div className={st.cont}>
                {soc.soc.map(i => <a href={i.link}><div style={{width:`40px`, height:`40px`,margin:`auto`}}>
                    <img className={st.img} src={i.img} alt={i.socName} />
                </div>
                </a>)}
            </div>
            )}
        </div>
    )
}

export default SocNetwork