import React from 'react'
import { NavLink } from 'react-router-dom';
import hl from './hotel.module.scss'
import * as axios from "axios";

function PrewHotel(props) {

    if (props.styleItems.length === 0) {
        axios.get('http://localhost:3333/thems').then(response => {
            props.styleHot(response.data)
        })
    }

    let logoElements = props.itemsLogo.map(logo =>
        <NavLink className={hl.logoLinkA} key={logo.id} to={logo.startLink}>
            <div>
                <div className={hl.contLog}>
                    <div className={hl.logText}>
                        <div className={`${hl.t} ${props.styleItems.map(st => st.subject)}-text text-lighten-5`} >D</div>
                        <div className={`${hl.t} ${props.styleItems.map(st => st.subject)}-text text-lighten-5`} >O</div>
                    </div>
                    {/* <img src={logo.logoImg} alt={logo.hotelName} /> */}
                    <div className={hl.contNameHotel}>
                        <h2 style={{transition:`.5s`}}className={`${props.styleItems.map(st => st.subject)}-text text-lighten-5`}>{logo.logoName}</h2>
                        <h5 className={`${props.styleItems.map(st => st.subject)}-text text-lighten-5`}>{logo.hotelName}</h5>
                    </div>
                </div>

                <h3>{logo.hotelSlogan}</h3>
            </div>
        </NavLink>

    )

    return (
        <div data-aos='fade-down'>
            {logoElements}
        </div>
    )
}



export default PrewHotel
