import React from 'react'
// import * as axios from "axios";
import abt from './aboutHotel.module.css'
import Galary from '../galary/Gelary';


function AboutHotel(props) {

    // if (!props.sStyle) {
    //     return <div>Загрузка стилей...</div>
    // }
    // debugger
    // if (!props.sAbout) {
    //     return <div>Загрузка информации об отеле...</div>
    // }

    if (!props.sStyle) {
        return <div>Загрузка стилей...</div>
    }

    // if (!props.sRoom) {
    //     return <div>Загрузка фотографий комнаты...</div>
    // }
// debugger
    let aboutElements = props.aboutUsItems.map(about => (
        <div key="about.id">
            <div>

                <h3>
                    {about.title}
                </h3>
                <p>
                    {about.text}
                </p>
            </div>
            <div style={{ width: '100%' }}>
                <img width='100%' height='450px'/*className={abt.image}*/ src={about.aboutImg} alt={about.title} />
            </div>
            <div>
                <h3>Концепция</h3>
                <p>Концепция работы заключается в создании комфортных условий для семейного отдыха с детьми, а также в оздоровлении наших гостей. Мы создали условия для увлекательного и качественного времяпрепровождения всей семьи. Высокий уровень сервиса поможет вам почувствовать себя любимыми и самыми желанными гостями. На большой территории курорта разместились коттеджи, выполненные в нормандском стиле. В тени деревьев находятся места отдыха, где вы можете отдохнуть в жаркий день и насладиться чудесным морским воздухом. Пляж отеля имеет собственную инфраструктуру, позволяющую, не выходя за его пределы, весело и с комфортом проводить время.</p>
            </div>

        </div>
    )
    )

    return (
        <div>
            {aboutElements}
            <h3>Галерея отеля</h3>
            <Galary sStyle={props.sStyle}/>

        </div>
    )

}



export default AboutHotel
