import s from './cfp.module.scss'

function CFpromotion(props) {
    return (
        <div className={s.cont}>
            <div>
                <div>
                    <h3>
                        Действие акции
                    </h3>
                    <p>
                        26.04.2021 — 20.05.2021
                    </p>
                </div>
                <div>
                    <h3>
                        Период проживания
                    </h3>
                    <p>
                        10.05.2021 — 31.05.2021
                    </p>
                </div>
            </div>
            <hr />
            <div>
                <h3>
                    Вы можете узнать подробности по телефону
                </h3>
                <a href="">
                    8 800 123 45 67
                </a>
                <p>
                    или
                </p>
                <a href="../../booking">
                    <div className={`${s.button} ${props.sStyle.map(s=>s.subject)} lighten-1`}>
                        <p className={`${props.sStyle.map(s=>s.subject)}-text text-lighten-5`}>Бронировать</p>
                    </div>
                </a>
            </div>
        </div>
    )
}

export default CFpromotion