import React from "react";
import { NavLink } from "react-router-dom";
import CContainer from "../../../home/contactShow/CContainer";
// import Contacts from '../../../contacts/Contacts'
import FR from './FR.module.scss'
import FRSContainer from "./frSlider/FRSContainer";



function FullRoom(props) {
    if (!props.sRoom) {
        return <div>Loading...</div>
    }
    // debugger
    return (
        <>
            <div>
                <div className={FR.bigImageBlock} style={{ backgroundImage: `url(${props.sRoom.fullRoom.img.imgPrew})` }}>
                    <div style={{ color: `#fff` }} className={FR.contTermsBlock}>
                        <h2 style={{ textAlign: `center` }}>{props.sRoom.roomName}</h2>
                        <div className={FR.contTerms}>
                            {props.sRoom.fullRoom.terms.map(item => <div className={FR.contTermItem}>
                                <div className={FR.contTerm}>
                                    <img src={item.icon} alt="icon" />
                                </div>
                                <div className={FR.contTermsAbout}>
                                    <p>{item.about}</p>
                                </div>
                            </div>
                            )}
                        </div>
                    </div>
                    <NavLink to='/booking' style={{ color: `#fff` }}>
                        <div className={`${props.sStyle.map(s => s.subject)} lighten-1 ${FR.btn}`} style={{ width: `100%` }}>
                            <p className={`${props.sStyle.map(s => s.subject)}-text text-lighten-6`}>
                                Забронировать
                            </p>
                        </div>
                    </NavLink>
                </div>
                <div className="main-cont">
                    <div className={FR.NORCont}>
                        <div className={FR.NORBlock}>
                            <h2>{props.sRoom.numberOfRoom}</h2>
                            <p>{props.sRoom.aboutPrew}</p>
                        </div>
                        <div className={`${FR.NORBlock} ${FR.TAMCont}`}>
                            <div>
                                <h3>В номер входит:</h3>
                                <div className={FR.TAMItemsCont}>
                                    {props.sRoom.fullRoom.about.map(t => <div key={t.item} className={FR.TAMItem}>
                                        <img src={t.icon} alt={t.item} />

                                        <p>
                                            {t.item}
                                        </p>
                                    </div>)}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div>
                        <FRSContainer
                            {...props}
                            sRoom={props.sRoom}
                            sStyle={props.sStyle}
                        />
                    </div>
                    <div className={FR.TBACont}>
                        {props.sRoom.about.map(a =>
                            <div key={a.name} className={FR.TBABlock}>
                                <h3>{a.name}</h3>
                                <hr />
                                <p>
                                    {a.about}
                                </p>
                            </div>)}
                    </div>

                    {/* <Contacts /> */}
                    <div style={{ maxWidth: `200px` }}>
                        <NavLink to='/rooms'>
                            <div className={`${props.sStyle.map(s => s.subject)} lighten-1 ${FR.btn}`}>
                                <p className={`${props.sStyle.map(s => s.subject)}-text text-lighten-6`}>Вернуться к номерам</p>
                            </div>
                        </NavLink>
                    </div>
                </div>
                <CContainer />
            </div>
        </>
    );
}
export default FullRoom