import React from 'react'
import CContainer from './contact/CContainer'
import PIContainer from './prewImage/PIContainer'
import HowTGet from './contact/howToGet/HowTGet';
import HotelServices from './contact/hotelServices/HotelServices';
import BackGroundFon from '../../hook/backGround';

function Contacts(props) {

   return (
      <div>
         <BackGroundFon>
            <PIContainer />
         </BackGroundFon>
         <div className="main-cont">
            <h2>Контакты</h2>
            <CContainer />
         </div>
         <HowTGet />
         <HotelServices />
      </div>
   )
}


export default Contacts