import React from 'react'
import { connect } from 'react-redux'
import FullService from './service/fullServices/FullService'
import { getService } from '../../redux/servicesReduser'
import { getStyle } from '../../redux/styleReducer'
import { withRouter } from 'react-router'



class ServisesContainer extends React.Component {

   componentDidMount() {
      let linkMore = this.props.match.params.linkMore

      this.props.getService(linkMore)
      this.props.getStyle()


   }

   render() {
      return (
         <FullService {...this.props} sService={this.props.sService} sStyle={this.props.sStyle} />
      )
   }
}
let mapStateToProps = (state) => ({
   sService: state.servicesPage.sService
})

let WithUrlDataContainerComponent = withRouter(ServisesContainer)

export default connect(mapStateToProps, { getService, getStyle })(WithUrlDataContainerComponent)
