import { connect } from "react-redux"
import Rooms from "./Rooms"
import { roomAC } from "../../../redux/roomsReduser"
import { styleAC } from "../../../redux/styleReducer";

let mapStateToProps = (state) => {
    
    return {
        roomsItem: state.roomsPage.roomsItem,
        styleItems: state.styleModule.styleItems
    }
}
let mapDispatchToProps = (dispatch) => {
    return {
        roomHot(roomsItem) {
            dispatch(roomAC(roomsItem))
        },
        styleHot(styleItems) {
            dispatch(styleAC(styleItems))
        }
    }
}

export const RContainer = connect(mapStateToProps, mapDispatchToProps)(Rooms);
export default RContainer;