import React from "react";
import { connect } from 'react-redux'
import AboutHotel from "./AboutHotel";
import { getStyle } from '../../../redux/styleReducer'
import { getAbout } from '../../../redux/aboutUsReducer'
// import { aboutAC } from '../../redux/aboutUsReducer'
// import { getRoom } from '../../redux/roomsReduser'


class AHContainer extends React.Component {

    componentDidMount() {
        this.props.getStyle()
        this.props.getAbout()
        // this.props.getRoom()
        // this.props.getStyle()
        // this.props.getStyle()

    }

    render() {

        return (
            <>
                <AboutHotel
                    {...this.props}
                    sStyle={this.props.sStyle}
                    sAbout={this.props.sAbout}
                    // sRoom={this.props.sRoom}
                />
            </>
        )
    }
}
let mapStateToProps = (state) => ({
    sStyle: state.styleModule.sStyle,
    sAbout: state.styleModule.sAbout,
    aboutUsItems: state.aboutUsPage.aboutUsItems,
    // sRoom: state.styleModule.sRoom,
})
// let mapDispatchToProps = (dispatch) => {
//         return {
//             aboutHot(aboutUsItems) {
//                 dispatch(aboutAC(aboutUsItems))
//             }
//         }
//     }

export default connect(mapStateToProps,/*mapDispatchToProps,*/ {
    getStyle,
     getAbout, 
    // getRoom
})(AHContainer)


