import * as axios from "axios";
import a from "./PrewImage.module.scss";

function PrewImage(props) {
    if (props.styleItems.length === 0) {
        axios.get('http://localhost:3333/thems').then(response => {
            props.styleHot(response.data)
        })
    }

    return (
        <div className={a.PrewImg} style={{ backgroundImage: `url(${props.styleItems.map(back => back.prewImgAboutUs)})` }}>
            <div className={a.title}>
                <h1>Информация об Отеле</h1>
            </div>
        </div>
    )
}

export default PrewImage