import React from 'react'
// import bkng from './bkng.module.css'


function Bkng(props) {


    let bookElements = props.bookingItem.map(bkId => <div key={bkId.id} id={bkId.bookingId}></div>)

    return (
        <div>
           
            {bookElements}
        </div>
    )

}



export default Bkng
