import React from 'react'
import mapS from './contact.module.css'
import FForm from './feedbackForm/FForm'
function Contact(props) {

   let contactElement = props.itemsContact.map(cnt => <div key={cnt.id}>

      <a target="_blank" rel="noreferrer" href={cnt.contact.link} >
         <div>
            <img src={cnt.contact.img} alt={cnt.contact.about} />
         </div>
         <div>
            {cnt.contact.text}
         </div>
      </a>
   </div>

   )
   let mapsElement = props.itemsMap.map(map => <div key={map.id}>
      <iframe className={mapS.mapIframe} title={map.mapTitle} src={map.mapLink} frameborder="0"></iframe>
   </div>

   )

   return (
      <div>
         <div className="main-cont">
            <div>
               {mapsElement}
            </div>
            <div style={{display:`flex`, justifyContent:`space-around`, alignItems:`center`, flexWrap:'nowrap'}}>
               <div>
                  <p>Координаты для GPS навигаторов: 44.8706214,38.1530211</p>
                  {contactElement}
               </div>
               <div>
                  <FForm/>
               </div>
            </div>
         </div>
      </div>
   )
}



export default Contact
