import React from 'react'
import serv from './service.module.css'
import * as axios from "axios";
import { NavLink } from 'react-router-dom';

function Service(props) {

    if (props.serviceItems.length === 0) {
        axios.get('http://localhost:3333/services').then(response => {
            props.serviceHot(response.data)
        })
    }
    if (props.styleItems.length === 0) {
        axios.get('http://localhost:3333/thems').then(response => {
            props.styleHot(response.data)
        })
    }

    let servicesElement = props.serviceItems.map(servi => <div key={servi.id} className={`${serv.contItemServ} ${props.styleItems.map(st => st.subject)} lighten-5`}>
        <div className={serv.imgTitle} style={{ backgroundImage: `url(${servi.img})` }}>
        </div>
        <div className={serv.descriptionCont}>
            <h2>{servi.nameService}</h2>
            <div></div>
            <div>
                {servi.about.mini.map(ab => <p key={ab.id}>{ab.text}</p>)}
            </div>
            <hr />
            <div>
                <p>
                    {servi.termsOfService}
                </p>
            </div>
            <div>
                <div>
                    <NavLink to={'/services/' + servi.linkMore}>
                        <div className={`${serv.btn} ${props.styleItems.map(st => st.subject)} lighten-1`}>
                            <p className={`${props.styleItems.map(st => st.subject)}-text text-lighten-5`}>Подробнее</p>
                        </div>

                    </NavLink>
                </div>
            </div>
        </div>
    </div>
    )

    return (
        <div className={serv.contServ}>
            {servicesElement}
        </div>
    )
}



export default Service
