import React from "react";
import { NavLink } from "react-router-dom";
import fs from './fullService.module.scss'
import CContainer from "../../../home/contactShow/CContainer";
import Galerys from "../../../../hook/Galerys";

function FullRoom(props) {
    if (!props.sService) {
        return <div></div>
    }

    let breadCrumbs1    = props.sService.breadCrumbs.lvlOne
    let breadCrumbs2    = props.sService.breadCrumbs.lvlTwo
    let title           = props.sService.nameService
    let aboutPage       = props.sService.about.page
    let aboutFull       = props.sService.about.full
    let aboutMini       = props.sService.about.mini
    let aboutUl         = props.sService.about.ul
    let slidImage       = props.sService.imgSlid
    let topImage        = props.sService.img

    return (
        <>
            <div>
                <div className={fs.fonImage} style={{ backgroundImage: `url(${topImage})` }}>

                    <div><h2>{title}</h2>
                        {aboutMini.map(abM =>
                            <p key={abM.id}>
                                {abM.text}
                            </p>
                        )}
                    </div>

                </div>
                <div className="main-cont">
                    <div className="breadCrumbsCont">
                        <NavLink key={breadCrumbs1.id} to={breadCrumbs1.link}>{breadCrumbs1.name}</NavLink> /
                    <NavLink key={breadCrumbs2.id} to={breadCrumbs2.link}>{breadCrumbs2.name}</NavLink> /
                        <b><p>{title}</p></b>
                    </div>
                    <h2>{title}</h2>
                    {aboutPage.map(abP => <div key={abP.id}>
                        <h3>{abP.title}</h3>
                        <p>{abP.text}</p>
                    </div>)}
                    {aboutFull.map(abF => <div key={abF.id}>
                        <h3>{abF.title}</h3>
                        <p>{abF.text}</p>
                    </div>)}
                    {aboutMini.map(abM => <div key={abM.id}>
                        <h3>{abM.title}</h3>
                        <p>{abM.text}</p>
                    </div>)}
                    {aboutUl.map(abU => <div key={abU.id}>
                        <h3>{abU.title}</h3>
                        {abU.text.map(li => <li key={li.id}>
                            {li.text}
                            </li>)}
                    </div>)}

                    <div>
                        <h3>Слайдер или галерея</h3>
                        <div className={fs.galaryCont}>
                            <Galerys>
                                {slidImage.map(sl =>
                                    <a href={sl.imgLink} key={sl.id}>
                                        <img width='300px' height='300px' src={sl.imgLink} alt="" />
                                    </a>
                                )}
                            </Galerys>
                        </div>

                    </div>

                    <div>
                        <NavLink to='/services'><button>Вернуться назад</button></NavLink>
                    </div>
                </div>
                <CContainer />
            </div>

        </>
    );

}

export default FullRoom