import React from 'react'
import pr from './promotion.module.css'
import * as axios from "axios";
import { NavLink } from 'react-router-dom';

function Promotion(props) {

    if (props.promotionItems.length === 0) {
        axios.get('http://localhost:3333/promotions').then(response => {
            props.promotionHot(response.data)
        })
    }
    if (props.styleItems.length === 0) {
        axios.get('http://localhost:3333/thems').then(response => {
            props.styleHot(response.data)
        })
    }
    // debugger
    let promotionsElement = props.promotionItems.map(promot => <div key={promot.id} className={`${pr.contBlock} ${props.styleItems.map(st => st.subject)} lighten-5`}>
         
        {/* {classList = pr.btn + promot.style + ' ' + 'lighten-1'} */}
        <NavLink className={pr.btnA} to={'/promotions/' + promot.linkMore}>
            <div className={pr.promoImg} style={{ backgroundImage: `url(${promot.promotions.mini.img})` }}></div>
        </NavLink>
        <div>
            <h2 className={pr.titleText}>{promot.title}</h2>
            <div>
                <p className={pr.about}>{promot.promotions.mini.about}</p>
            </div>
            <hr className={pr.hr}/>
            <div className={`${pr.termOfAction} ${props.styleItems.map(st => st.subject)}-text text-lighten`}>
                СРОК АКЦИИ: {promot.data} <br />
                Период проживания: {promot.accommodation}
            </div>
            <div>
                <NavLink className={pr.btnA} to={'/promotions/' + promot.btn.linkBtn}>
                    <div className={`${pr.btn} ${props.styleItems.map(st=>st.subject)} lighten-1`}>
                        <p className={`${props.styleItems.map(st => st.subject)}-text text-lighten-5`}>{promot.btn.nameBtn}</p>
                    </div>
                </NavLink>
            </div>
        </div>
    </div>
    )
    // let breadCrumbs = props.promotionItems.map(bread => <NavLink key={bread.id} to={bread.breadCrumbs.lvlOne.link}>{bread.breadCrumbs.lvlOne.name}
    // </NavLink>
    //     /
    //     <b>{bread.title}</b>
    // )

    return (

        <div className={pr.cont}>
            {/* <div>
                {breadCrumbs}
            </div> */}
            {promotionsElement}
        </div>
    )
}



// let serviceElement = props.itemsServices.map(srvs => <div key={srvs.id} className={serv.cont_item}>
//     <div>
//         <img className={serv.image} src={srvs.img} alt={srvs.serviceName} />
//     </div>
//     <div>
//         <h3>{srvs.serviceName}</h3>
//         <p>{srvs.serviceAbout}</p>
//     </div>
// </div>
// )

// return (
//     <div className={serv.cont}>
//         {serviceElement}
//     </div>

// )
// }



export default Promotion
