import React from 'react'
import m from './m.module.scss'

class BookingModule extends React.Component {
    
    componentWillMount(props) {
        // let sty = this.props.styleItems.map(st=>st.subject)
        //     console.log(sty)
     
    }
    componentDidMount(){
        var head = document.getElementsByTagName('head')[0]
        var s = document.createElement('script')
   
        s.type = 'text/javascript';
        s.id = 'ea-widget-connect'
        s.async = true;
        s.src = `https://trip-advance.ru/integrations/widgets/connect.js`
        s.src += `?idb=ea-widget`
        s.src += `&id=`             + `2fcd0e10-bada-11e9-80c7-000000538429` //ID Hotel
        s.src += `&style=`          + `blue`                                 //STYLE Widget
        s.src += `&widget=`         + `wdHotel`                              //type widget
        s.src += `&lang=`           + `ru`                                   //lang
        s.src += `&datepicker=`     + `1`                                    //date block
        s.src += `&guestchoice=`    + `1`                                    //gues block
        s.src += `&opacity=`        + `0.8`                                  //opacity style
        s.src += `&href=`           + `/booking`                             //link href bookingPage
        s.src += `&theme=` + `{"pc":"198","ps":"17","pl":"1","ptc":"199","pts":"22","ptl":"0.18","sc":"193","ss":"83","sl":"0.84","stc":"173","sts":"100","stl":"0","pbr":"8","sbr":"8"}`         //theme style
        head.appendChild(s)
    }

    render() {
        return (
            <>
                <div className={m.item} id="ea-widget"></div>
            </>
        )
    }
}

export default BookingModule