import React from 'react'
import BContainer from './bkng/BContainer'
import PIContainer from './prewImage/PIContainer'
import { connect } from 'react-redux'

class Booking extends React.Component {


   componentDidMount() {
   
      var s = document.createElement('script')
      s.type = 'text/javascript';
      s.id = 'ea-ta-spcmdl'
      s.async = true;
      s.src = `https://trip-advance.ru/integrations/link/spcmdl.js`
      s.src +=`?id=2fcd0e10-bada-11e9-80c7-000000538429`
      s.src +=`&showRooms=true`
   
      s.src +=`&style=cyan`
   
      s.src +=`&sectionId=ea-booking`
      s.src +=`&viewType=roomType`
      s.src +=`&iframe=inline`
      s.src +=`&v=1`
      s.src +=`&basket=false`
      var head = document.getElementsByTagName('head')[0]
      head.appendChild(s) 
   
    }
   render(

   ) {
      if (!this.props.sStyle) {
         return <div>load...</div>         
      }
           
      return (
         <div>
            <PIContainer />
            <div className="main-cont">
               <h1>Бронирование</h1>
               <BContainer sStyle={this.props.sStyle} />
            </div>
         </div>
      )
   }

}

let mapStateToProps = (state) => ({

})

export default connect(mapStateToProps)(Booking)




