import { connect } from "react-redux"
import Promotion from './Promotion'
import { promotionAC } from "../../../redux/homeReducer"
import { styleAC } from "../../../redux/styleReducer";


let mapStateToProps = (state) => {

    return {
        promotionItems: state.homePage.promotionItems,
        styleItems: state.styleModule.styleItems
    }
}
let mapDispatchToProps = (dispatch) => {
    return {
        promotionHot(promotionItems) {
            dispatch(promotionAC(promotionItems))

        },
        styleHot(styleItems) {
            dispatch(styleAC(styleItems))
        }
    }
}
export const PContainer = connect(mapStateToProps, mapDispatchToProps)(Promotion);
export default PContainer;