import React from 'react'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from 'react-slick'
import sl from './slider.module.scss';

function MainSlides(props) {
   const settings = {
      dots: false,
      focusOnSelect: true,// щелкнуть на слайд чтобы прокрутилось до него
      fade: true, //одно изображение при true
      lazyLoad: true, //медленная загрузка
      pauseOnHover: true, //Пауза при наведении
      infinte: true, // Неравномерные множества
      speed: 750, //скорость перелистывания
      slidesToShow: 2, //Количество видимых слайдов сразу
      arrows: false, //Стрелки вперёд назад
      slidesToScroll: 1, //Количество перелист блоков за раз
      className: "topSlides", //Класс у контейнера
      autoplay: true, //авто запуск
      autoplaySpeed: 7000,//врямя до первого авто запуска потом нужно вычитать скорость прокрутки
   }
let mainSlider = props.itemSlide.map(ms => <div key={ms.id} >
   <div className={sl.slidImage} style={{backgroundImage:`url(${ms.urlPhoto})`}}></div>
</div>)
   return (
      // <Slider {...settings}>
      //    {mainSlider}
      // </Slider>

      <Slider style={{overflow:'hidden', height:'90vh'}} {...settings}>
         {mainSlider}
         </Slider>


   )
}

export default MainSlides
