import React from "react";
import { connect } from 'react-redux'
import { getStyle } from '../../redux/styleReducer'
import { getBasement } from '../../redux/footerReduser'
import Footer from "./Footer";


class FContainer extends React.Component {

    componentDidMount() {
        this.props.getStyle()
        this.props.getBasement()
    }

    render() {

        return (
            <>
                <Footer
                    {...this.props}
                    sStyle={this.props.sStyle}
                    sBasement={this.props.sBasement}
                    itemsMenu={this.props.itemsMenu}
                    basementItems={this.props.basementItems}
                />
            </>
        )
    }
}
let mapStateToProps = (state) => ({
    sStyle: state.styleModule.sStyle,
    sBasement: state.footerModule.sBasement,
    basementItems: state.footerModule.basementItems,
    itemsMenu: state.headerPage.itemsMenu,
    
})
  
export default connect(mapStateToProps, {
    getStyle,
    getBasement

})(FContainer)







