import React from 'react'
import { connect } from 'react-redux'
import FullRoom from './room/fullRoom/FullRoom'
import { getRoom } from '../../redux/roomsReduser'
import { getStyle } from '../../redux/styleReducer'
import { withRouter } from 'react-router'



class RoomsContainer extends React.Component {

   componentDidMount() {
      
      let linkMore = this.props.match.params.linkMore

      this.props.getRoom(linkMore)
      this.props.getStyle()
   }
      render() {
         return (
            <FullRoom {...this.props} sRoom={this.props.sRoom} sStyle={this.props.sStyle} />
         )
      }
   }
let mapStateToProps = (state) => ({
   sRoom: state.roomsPage.sRoom,
   sStyle: state.styleModule.sStyle
})

let WithUrlDataContainerComponent = withRouter(RoomsContainer)

export default connect(mapStateToProps, { getRoom, getStyle })(WithUrlDataContainerComponent)
