import React from 'react'
import { connect } from 'react-redux'
import FullPromotion from './promotion/fullPromotion/FullPromotion'
import { getPromotion } from '../../redux/homeReducer'
import { getStyle } from '../../redux/styleReducer'
import { withRouter } from 'react-router'





class PromotionsContainer extends React.Component {

   componentDidMount() {
      let linkMore = this.props.match.params.linkMore

      this.props.getPromotion(linkMore)
      this.props.getStyle()
   
   }

   render() {
      return (
         <>
            <FullPromotion {...this.props} sPromotion={this.props.sPromotion} sStyle={this.props.sStyle} />
         </>
      )
   }
}

let backGroundFon = (props) => {

   return <PromotionsContainer {...props} />
}

// debugger
let mapStateToProps = (state) => ({
   sPromotion: state.homePage.sPromotion,
   sStyle: state.styleModule.sStyle
})

let WithUrlDataContainerComponent = withRouter(backGroundFon)

export default connect(mapStateToProps, { getPromotion, getStyle })(WithUrlDataContainerComponent)
