import React from "react";

function Phone(props) {
    let sStyle = props.sStyle.map(s => s.subject)


    return(
        <div>
        {props.phone.map(p=> <a className={`${sStyle}-text text-lighten-6`} key={p.id} href={p.phone.map(l=>l.link)}>{p.phone.map(t=>t.text)}</a> )}
        </div>
    )
}

export default Phone