import * as axios from "axios";
import p from './promo.module.scss'
import { NavLink } from 'react-router-dom';

function Promotion(props) {
    if (props.promotionItems.length === 0) {
        axios.get('http://localhost:3333/promotions').then(response => {
            props.promotionHot(response.data)
        })
    }
    if (props.styleItems.length === 0) {
        axios.get('http://localhost:3333/thems').then(response => {
            props.styleHot(response.data)
        })
    }

    let promo = props.promotionItems.map(pr => <div className={`${p.contItem} ${props.styleItems.map(s => s.subject)} lighten-5`} key={pr.id}>
        <NavLink to={`/promotions/${pr.linkMore}`}>
            <div className={p.imgPromotion} style={{ backgroundImage: `url(${pr.promotions.mini.img})` }}>
            </div>
        </NavLink>
        <div className={p.textZona}>
            <div className={p.textTitle}><h4 className={`${props.styleItems.map(st => st.subject)}-text text-accent-4`} >{pr.title}</h4></div>
            <div className={`${p.textAbout} ${props.styleItems.map(st => st.subject)}-text text-darken-1`}><p>{pr.promotions.mini.about}</p></div>
            <div className={`${p.btn} ${props.styleItems.map(st => st.subject)} lighten-1`}>
                <NavLink className={`${props.styleItems.map(st => st.subject)}-text text-lighten-6`} to={`/promotions/${pr.linkMore}`}>
                    <p>Подробнее</p>
                </NavLink>
            </div>
        </div>
    </div>)
    return (
        <div>
            <h2>Наши предложения</h2>
            <div className={p.cont}>
                {promo}
            </div>
        </div>
    )
}
export default Promotion