import React from "react";
import { NavLink } from "react-router-dom";
import log from "./logo.module.scss";

function Logotype(props) {

    // let logoImg         = l.logHotel
    // let logoName        = l.logHotel
    // let hotelName       = l.logHotel
    // let firstLetter     = l.logHotel
    // let secondLetter    = l.logHotel
let sStyle = props.sStyle.map(s=>s.subject)
    return (
        <div>
            {props.logo.map(l => <NavLink to='/' key={l.id} >
                <div className={log.cont}>
                    <div>
                        {/* <div>{l.logHotel.map(lo => lo.logoImg)}</div> */}
                        <div className={`${log.contLogoDO} ${sStyle}-text text-lighten-6`}>
                            <div>{l.logHotel.map(lo => lo.firstLetter)}</div>
                            <div>{l.logHotel.map(lo => lo.secondLetter)}</div>
                        </div>
                    </div>
                    <div className={`${log.contText} ${sStyle}-text text-lighten-6`}>
                        <div>
                            <h3>
                                {l.logHotel.map(lo => lo.logoName)}
                            </h3>
                        </div>
                        <div>
                            <h4>
                            {l.logHotel.map(lo => lo.hotelName)}
                            </h4>
                        </div>
                    </div>
                </div>
            </NavLink>)}
        </div>
    )
}

export default Logotype