import htg from './HTG.module.scss'


function HowTGet(props) {
    return (
        <div style={{backgroundColor:`#d6d6d6`}}>
            <h3 style={{textAlign:`center`}}>Как до нас добраться</h3>
            <div className={htg.howCont}>
                <div className={htg.howBlock}>
                    <h4>От аэропорта</h4>
                    <p>от аэропорта города анапа маршрутное такси № 113 до автовокзала, до указателя  «Demo hetel «КОСМОС»»</p>
                    <p>время в пути: 15 мин. (14 км)</p>
                </div>
                <div className={htg.howBlock}>
                    <h4>От ж/д вокзала</h4>
                    <p>от ж/д вокзала города анапа маршрутные такси № 100 до указателя «Demo hetel «КОСМОС»»</p>
                    <p>время в пути: 5 мин. (4.9 км)</p>
                </div>
                <div className={htg.howBlock}>
                    <h4>От автовокзала</h4>
                    <p>от автовокзала города анапа (ост. «центральный рынок») - маршрутные такси № 100 до указателя  «Demo hetel «КОСМОС»»</p>
                    <p>время в пути: 1.5 мин. (1 км)</p>
                </div>
            </div>
        </div>
    )
}

export default HowTGet