import React from 'react'
import SLContainer from './contactsPhone/SLContainer'
import PHContainer from './logoPrewHotel/PHContainer';
import TMContainer from './rootMenu/tMContainer';
import header from './header.module.scss'
import SNHContainer from './socNetHeader/SNHContainer';

function Header(props) {
    if (!props.sStyle) {
        return <div></div>
    }

    return (

        <header className={`${header.cont} ${props.sStyle.map(st => st.subject)} lighten-6`}>
            <div style={{ width: '100%' }}>
                <div className={header.logotype}>
                    <SLContainer />
                    <PHContainer />
                    <SNHContainer />
                </div>
                <div className={header.menu}><TMContainer /></div>
            </div>
        </header>
    )
}



export default Header
