import * as axios from "axios";
import r from "./PrewImage.module.scss";

function PrewImage(props) {
    if (props.styleItems.length === 0) {
        axios.get('http://localhost:3333/thems').then(response => {
            props.styleHot(response.data)
        })
    }

    return (
        <div className={r.PrewImg} style={{ backgroundImage: `url(${props.styleItems.map(back=>back.prewImgRooms)})`}}>
<div className={r.title}><h1>Номерной фонд</h1></div>
        </div>
    )
}

export default PrewImage