import {
    hotelApi
} from "../api/api"

const STYLE = 'STYLE'
const SET_STYLE = 'SET_STYLE'

let initionState = {
    styleItems: [

],
    sStyle: null,
}

const styleReducer = (state = initionState, action) => {

    switch (action.type) {
        case STYLE: {
            return {
                ...state,
                styleItems: [ /*...state.styleItems,*/ ...action.styleItems]
            }
        }
        case SET_STYLE: {
            return {
                ...state,
                sStyle: action.sStyle
            }
        }
        default:
            return state;
    }
}

export const styleAC = (styleItems) => ({
    type: STYLE,
    styleItems
})
const setStyle = (sStyle) => ({
    type: SET_STYLE,
    sStyle
})

export const getStyle = () => (dispatch) => {
    hotelApi.getStyle().then(response => {
        dispatch(setStyle(response.data))
    })
}

export default styleReducer;