import { connect } from "react-redux"
import PrewHotel from './PrewHotel'
import { styleAC } from "../../../redux/styleReducer";

let mapStateToProps = (state) => {
    return {

        itemsLogo: state.headerPage.itemLogotype,
        styleItems: state.styleModule.styleItems
    }
}
let mapDispatchToProps = (dispatch) => {
    return {
        styleHot(styleItems) {
            dispatch(styleAC(styleItems))
        }
        }
    }

export const PHContainer = connect(mapStateToProps, mapDispatchToProps)(PrewHotel);
export default PHContainer;