import * as axios from "axios";
import p from "./PrewImage.module.scss";

function PrewImage(props) {
    if (props.styleItems.length === 0) {
        axios.get('http://localhost:3333/thems').then(response => {
            props.styleHot(response.data)
        })
    }

    return (
        <div className={p.PrewImg} style={{ backgroundImage: `url(${props.styleItems.map(back => back.prewImgPromotions)})` }}>
            <div className={p.title}>
                <h1>Акции отеля</h1>
            </div>
        </div>
    )
}

export default PrewImage