import React from 'react'
import { NavLink } from 'react-router-dom';
import rm from './room.module.scss'
import * as axios from "axios";

function Rooms(props) {


    if (props.roomsItem.length === 0) {
        axios.get('http://localhost:3333/rooms').then(response => {
            props.roomHot(response.data)
        })
    }

    if (props.styleItems.length === 0) {
        axios.get('http://localhost:3333/thems').then(response => {
            props.styleHot(response.data)
        })
    }

    // {`${rm.contRoom} ${props.roomsItem.position}`}
    // debugger
    let roomsElement = props.roomsItem.map(room => <div key='room.id' className={`${rm.contRoom} ${room.position}`} style={{ flexDirection: `${room.position}` }}>
        <NavLink className={rm.imgRoomLink} style={{width:'100%'}} to={'/rooms/' + room.linkMore}>
            <div className={rm.imgRoomTitle} style={{ backgroundImage: `url(${room.roomMini.imgPrew})` }}>

            </div>
        </NavLink>
        <div className={`${rm.descriptionCont} ${props.styleItems.map(st => st.subject)} lighten-5`} >
            <h2>{room.roomName}</h2>
            <div className={rm.termsCont}>
                {room.roomMini.terms.map(t => <div key={props.roomsItem.id} className={rm.termAbout}>
                    <div className={rm.termImg}>
                        <img src={t.icon} alt={t.about}/>
                    </div>
                    <span>{t.about}</span>
                </div>)}
            </div>
            <div>
                <p>{room.roomMini.aboutRoom}</p>
            </div>
            <div className={rm.contBtn}>
                <NavLink className={`${rm.btn} ${props.styleItems.map(st => st.subject)} lighten-1`} to={room.bookingLink}><div className={rm.btnBlock}>
                    <p className={`${props.styleItems.map(st => st.subject)}-text text-lighten-5`}>Забронировать</p>
                </div>
                </NavLink>

                <NavLink className={`${rm.btn} ${props.styleItems.map(st => st.subject)} lighten-1`} to={'/rooms/' + room.linkMore}><div className={rm.btnBlock}>
                    <p className={`${props.styleItems.map(st => st.subject)}-text text-lighten-5`}>Подробнее</p>
                </div>
                </NavLink>
            </div>
        </div >
    </div >
    )

    return (
        <div className={rm.cont}>
            {roomsElement}
        </div>
    )
}


export default Rooms
