import { NavLink } from "react-router-dom"
import nav from '../topMenu.module.css'

function Menu(props) {


    let menuElement = props.itemsMenu
        .map(menu =>
            <NavLink className={`${nav.item} aos-init`}
                key={menu.id}
                to={menu.hrefLink}
                activeClassName={nav.activeLink}>
                <p>{menu.item}</p>
            </NavLink>
        )
        // debugger
    return (
        <>
            <div className={props.active ? 'menu  active' : 'menu'} onClick={() => props.setActive(false)}>
                {/* <div className="blur"/> */}
                    <div className="menu__content">
                        <div className="menu__header">{props.header}</div>
                        {menuElement}
                    </div>
               
            </div>
        </>
    )

}
export default Menu