
import React from "react";
import { connect } from "react-redux";
import { getStyle } from "../../redux/styleReducer";
import BookingModule from "./Module";

class MContainer extends React.Component {
    componentDidMount() {
        this.props.getStyle() 
    }
    render() {
        return (
            <>
                <BookingModule  {...this.props} sStyle={this.props.sStyle} styleItems={this.props.styleItems}/>
            </>
        )
    }
}
let mapStateToProps = (state) => ({
    sStyle: state.styleModule.sStyle,
    styleItems: state.styleModule.styleItems
}
)


export default connect(mapStateToProps, { getStyle })(MContainer)