import React, { useState } from 'react'
import nav from './topMenu.module.css'
// import { NavLink } from 'react-router-dom'
import Menu from './menu/Menu'

function TopMenu(props) {



    const [menuActive, setMenuActive] = useState(false)
    // debugger
    return (
        <>

            <div className={`${nav.burgerMenu}`} onClick={() => setMenuActive(!menuActive)}>
                <span />
            </div>
            <nav className='menuBody'>
                <Menu active={menuActive} setActive={setMenuActive} header={'Меню'} itemsMenu={props.itemsMenu} />
            </nav >


        </>
    )
}



export default TopMenu
