import * as axios from "axios"



const instance = axios.create({

    baseURL: 'http://localhost:3333/',

})

const testInstance = axios.create({
    baseURL:'http://localhost:3000/static/js/C:/react/projects/test_hotel/public/json/'
})

export const hotelApi = {
    getBasement(){
        return testInstance.get(`basement` )
    },
    getRoom(linkMore){
        return instance.get(`rooms/`+ linkMore)  
    },
    getPromotion(linkMore){
        return instance.get(`promotions/` + linkMore)
    },
    getService(linkMore){
        return instance.get(`services/` + linkMore)
    },
    getStyle(){
        return instance.get(`thems/` )
    },
    getAbout(){
        return instance.get(`posts/` )
    },

}

