import * as axios from "axios";
import c from "./PrewImage.module.scss";


function PrewImage(props) {

    if (props.styleItems.length === 0) {
        axios.get('http://localhost:3333/thems').then(response => {
            props.styleHot(response.data)
        })
    }

    return (
        <div className={c.PrewImg} style={{ backgroundImage: `url(${props.styleItems.map(back => back.prewImgContact)})` }}>
            <div className={c.title}>
                <h1>Контакты отеля</h1>
            </div>
        </div>
    )
}

export default PrewImage