import {
    hotelApi
} from "../api/api"

const SERVICE_RED = 'SERVICE-RED'
const FULL_SERVICE = 'FULL-SERVICE'
const SET_SERVICE = 'SET-SERVICE'

let initionState = {
    serviceItems: [],
    mapsItem: [{
        id: 1,
        mapLink: 'https://yandex.ru/map-widget/v1/-/CCuURT24',
        mapTitle: 'Адрес Демо ОТЕЛЬ «Космос»',
    }],
    sService: null,
}

const servicesReduscer = (state = initionState, action) => {
    switch (action.type) {
        case SERVICE_RED: {
            return {
                ...state,
                serviceItems: [ /*...state.serviceItems,*/ ...action.serviceItems]
            }
        }
        case FULL_SERVICE: {
            return {
                ...state,
                serviceItems: [ /*...state.serviceItems,*/ ...action.serviceItems]
            }
        }
        case SET_SERVICE: {
            return {
                ...state,
                sService: action.sService
            }
        }
        default:
            return state;
    }
}
export const serviceAC = (serviceItems) => ({
    type: SERVICE_RED,
    serviceItems
})
const setService = (sService) => ({
    type: SET_SERVICE,
    sService
})

export const getService = (linkMore) => (dispatch) => {
    hotelApi.getService(linkMore).then(response => {
        dispatch(setService(response.data))
    })
}
export default servicesReduscer;