import { connect } from "react-redux"
// import { serviceAC } from "../../../redux/servicesReduser"
import { styleAC } from "../../../redux/styleReducer";
import PrewImage from "./PrewImage"

let mapStateToProps = (state) => {

    return {
        // serviceItems: state.servicesPage.serviceItems,
        styleItems: state.styleModule.styleItems
    }
}
let mapDispatchToProps = (dispatch) => {
    return {
        // serviceHot(serviceItems) {
        //     dispatch(serviceAC(serviceItems))
        // },
        styleHot(styleItems) {
            dispatch(styleAC(styleItems))
        }
    }
}

export const PIContainer = connect(mapStateToProps, mapDispatchToProps)(PrewImage)
export default PIContainer;