import React, { useEffect, useState } from "react"
import gl from './galary.module.scss'
import Galerys from '../../../hook/Galerys'

const images = [
    {
        id: '1',
        src: 'https://ae01.alicdn.com/kf/HTB11CzTXx2rK1RkSnhJq6ykdpXak/FFFAS-3D.jpg',
        alt: 'anime girl sexy',
        tag: 'Отель'
    },
    {
        id: '2',
        src: 'https://steamuserimages-a.akamaihd.net/ugc/988989114496528120/586E2268E3A4EE4A55B688EEC751414482588518/',
        alt: 'anime girl sexy',
        tag: 'Территория'
    },
    {
        id: '3',
        src: 'https://pm1.narvii.com/6079/af8f7df8d87b43cfe21b44a0482313bb92a314cc_hq.jpg',
        alt: 'anime girl sexy',
        tag: 'Номера'
    },
    {
        id: '4',
        src: 'http://pa1.narvii.com/6385/55d42bb89e7accae9c74283135dc970f2e591a75_00.gif',
        alt: 'anime girl sexy',
        tag: 'Отель'
    },
    {
        id: '5',
        src: 'https://ae01.alicdn.com/kf/HLB1Gzr2JZfpK1RjSZFOq6y6nFXap.jpg',
        alt: 'anime girl sexy',
        tag: 'Территория'
    },
]
function Galary(props) {

    const [tag, setTag] = useState('Все фоторафии')
    const [filteredImages, setFilteredImages] = useState([])

    useEffect(() => {
        tag === 'Все фоторафии' ? setFilteredImages(images) : setFilteredImages(images.filter(image => image.tag === tag))
    }, [tag])
    
    return (
        <div handleSetTag={setTag} >
            <div className={gl.btnCont}>
                <TagButton sStyle={props.sStyle} name={'Все фоторафии'} handleSetTag={setTag} tagActive={tag === 'Все фоторафии' ? true : false} />
                <TagButton sStyle={props.sStyle} name={'Отель'} handleSetTag={setTag} tagActive={tag === 'Отель' ? true : false} />
                <TagButton sStyle={props.sStyle} name={'Территория'} handleSetTag={setTag} tagActive={tag === 'Территория' ? true : false} />
                <TagButton sStyle={props.sStyle} name={'Номера'} handleSetTag={setTag} tagActive={tag === 'Номера' ? true : false} />
            </div>
            <Galerys>
                {filteredImages.map(image => (
                    <a href={image.src}>
                        <img key={image.id} width='200px' height='200px' src={image.src} alt={image.alt} />
                    </a>
                )
                )}
            </Galerys>
        </div>
    )
}

const TagButton = ({ name, handleSetTag, tagActive, sStyle }) => {
    return <button className={`tag ${sStyle.map(s=>s.subject)} lighten-1 ${tagActive ? 'active' : null}`} onClick={() => handleSetTag(name)} >{name.toUpperCase()}</button>
}

export default Galary