import React from "react";

function Email(props) {
    let sStyle = props.sStyle.map(s => s.subject)

    return(
        <div>
            <a className={`${sStyle}-text text-lighten-6`} href={props.email.map(m=>m.mail)}>
                {props.email.map(m=>m.mail)}
                </a>
        </div>
    )
}

export default Email