import React from 'react'
import Slider from "react-slick";
import { SRLWrapper } from 'simple-react-lightbox';
import FRS from './FRSlider.module.scss'
// import * as axios from "axios";


function FRSlider(props) {

    // {/* ${props.styleItems.map(st => st.subject)} lighten-1}`*/} 
    const settings = {
        dots: true,                 // точки
        focusOnSelect: false,       // щелкнуть на слайд чтобы прокрутилось до него
        fade: false,                // одно изображение при true
        lazyLoad: true,             // медленная загрузка
        pauseOnHover: true,         // Пауза при наведении
        infinte: true,              // Неравномерные множества
        speed: 750,                 // скорость перелистывания
        slidesToShow: 1,            // Количество видимых слайдов сразу
        arrows: false,              // Стрелки вперёд назад
        slidesToScroll: 1,          // Количество перелист блоков за раз
        className: "roomSlides",    // Класс у контейнера
        autoplay: false,            // авто запуск
        //autoplaySpeed: 7000,        // врямя до первого авто запуска потом нужно вычитать скорость прокрутки
        appendDots: dots => (
            <div
                style={{
                    borderRadius: "10px",
                    padding: "10px"
                }}
            >
                <ul className={FRS.styleLi} style={{ margin: "0px" }}> {dots} </ul>
            </div>
        ),
        customPaging: i => (
            <div className={`${FRS.dotsRooms} ${props.sStyle.map(s => s.subject)} lighten-1`} >
            </div>
        )
    }
    // debugger
    return (
// Слайдер и показ фото пересмотреть!!!
        <Slider {...settings}>
            {props.sRoom.fullRoom.img.imgBigSlid.map(sl => <SRLWrapper>
                <div className={FRS.sliderImgBig} >

                    <a href={sl.img}>
                        <div className={FRS.sliderImg}>
                            <img width='100%' src={sl.img} alt="" />
                        </div>
                    </a>
                </div>
            </SRLWrapper>)}

        </Slider>

    );
}
export default FRSlider