import React from "react";
import { connect } from 'react-redux'
import Booking from "./Booking";
import { getStyle } from '../../redux/styleReducer'


class BContainer extends React.Component {
    
    componentDidMount() {
            this.props.getStyle()      
    }

    render() {

        return (
            <>
                <Booking {...this.props} sStyle={this.props.sStyle} />
            </>
        )
    }
}
let mapStateToProps = (state) => ({
    sStyle: state.styleModule.sStyle,
    styleItems: state.styleModule.styleItems
})

export default connect(mapStateToProps, { getStyle })(BContainer)