import React from 'react'
import footer from './footer.module.scss'
// import * as axios from 'axios';
import TMContainer from "../header/rootMenu/tMContainer";
import SocNetwork from './socNetwork/SocNetwork';
import Email from './email/Email';
import Logotype from './logotype/Logotype';
import Phone from './phone/Phone';
// import { NavLink } from 'react-router-dom';

function Footer(props) {

    // if (props.styleItems.length === 0) {
    //     axios.get('http://localhost:3333/thems').then(response => {
    //         props.styleHot(response.data)
    //     })
    // }
    // debugger
    if (!props.sStyle) {
        return <div>Загрузка стилей...</div>
    }

    // debugger
    return (
        <footer className={`${footer.cont} ${props.sStyle.map(st => st.subject)}  darken-5`}>
            <div>
                
                <TMContainer />
            </div>
            <hr/>
            <div className={footer.infoBlock}>
                <Logotype   sStyle={props.sStyle} logo={props.basementItems}    />
                <Phone      sStyle={props.sStyle} phone={props.basementItems}   />
                <Email      sStyle={props.sStyle} email={props.basementItems}   />
                <SocNetwork sStyle={props.sStyle} soc={props.basementItems}     />
            </div>
            <hr/>
            <div className={footer.protectionCont}>
                <div className={`${footer.protection} ${props.sStyle.map(st => st.subject)}-text  text-lighten-6`}>© 2021 Все права защищены.</div>
                <div className={footer.prewTACont}>
                    <a rel="noreferrer" target="_blank" href="https://trip-advance.ru">
                        <div className={footer.TACont}>
                            <div className={footer.TAItem}>T</div>
                            <div className={footer.TAItem}>A</div>
                        </div>
                    </a>
                    <a rel="noreferrer" target="_blank" href="https://trip-advance.ru">
                        <div className={footer.linkTA}>
                            <div className={`${props.sStyle.map(st => st.subject)}-text  text-lighten-6`}>Trip</div>
                            <div className={`${props.sStyle.map(st => st.subject)}-text  text-lighten-6`}>Advance</div>
                            <div className={`${footer.online} ${props.sStyle.map(st => st.subject)}-text text-lighten-6`}>Online бронирование</div>
                        </div>
                    </a>
                </div>
            </div>
        </footer >
    )
}



export default Footer
