import * as axios from 'axios';
import React from 'react'
import { NavLink } from 'react-router-dom'
import cnt from "./cnt.module.scss";


function Contact(props) {
    if (props.styleItems.length === 0) {
        axios.get('http://localhost:3333/thems').then(response => {
            props.styleHot(response.data)
        })
    }
    let contactElement = props.itemsContact.map(cntc => <div key={cnt.id}>

        <a className={`${props.styleItems.map(s => s.subject)}-text text-lighten-5`} target="_blank" rel="noreferrer" href={cntc.contact.link} >
            <div className={cnt.link}>
                <div className={cnt.linkImg}>
                    <img src={cntc.contact.img} alt={cntc.contact.about} />
                </div>
                <div className={cnt.linkText}>
                    {cntc.contact.text}
                </div>
            </div>
        </a>
    </div>

    )
    let mapsElement = props.itemsMap.map(map => <div key={map.id}>
        <iframe className={cnt.mapIframe} title={map.mapTitle} src={map.mapLink} frameborder="0"></iframe>
    </div>

    )

    return (
        <div className={`${cnt.cont}  ${props.styleItems.map(s => s.subject)} darken-1`}>
            <div className={cnt.itemMap}>
                {mapsElement}
            </div>
            <div className={cnt.itemContact}>
                <div>
                    <h2 className={`${props.styleItems.map(s => s.subject)}-text text-lighten-5`}>
                        Контакты
                        </h2>
                </div>
                <div>
                    <p className={`${props.styleItems.map(s => s.subject)}-text text-lighten-5`}>Координаты для GPS навигаторов: 44.8706214,38.1530211</p>
                    <div>
                        {contactElement}
                    </div>
                </div>
                <div>
                    <NavLink className={`${props.styleItems.map(s => s.subject)}-text text-lighten-5`} to="/contacts"><u>Подробная контактная информация</u></NavLink>
                </div>
            </div>

        </div>
    )
}



export default Contact
